/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Component" }; // TODO: adjust namespace

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    // Common Card options:
    // backgroundHeader="Transparent"
    // backgroundBody="Transparent"
    // border="0"
    // align="left"
    // maxWidth="700px"
    // header={false}
    return <Card title={t("Component")}></Card>;
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
